.contact__container {
    grid-template-columns:repeat(2, max-content) ;
    justify-content: center;
    column-gap: 6rem;
    padding-bottom: 3rem;
}

.contact__title {
    text-align: center;
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1-5);
}

.contact__info {
    display: grid;
    row-gap: 1rem;
    grid-template-columns:300px ;
}

.contact__form {
    width: 360px;
}

.contact__card {
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 1rem;
    border-radius: 0.75rem;
    text-align: center;
}

.contact__card-icon {
    font-size: 2rem;
    color: rgb(47, 44, 44);
    margin-bottom: var(--mb-0-25);

}

.contact__card-title,
.contact__card-data {
    font-size: var(--small-font-size);
}

.contact__card-title {
    font-weight: var(--font-medium);
}

.contact__card-data{
    display: block;
    margin-bottom: var(--mb-0-75);
}


.contact__button {
    
    color:rgb(47, 44, 44);
    font-size: var(--small-font-size);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    column-gap: 0.25rem;
}

.contact__button-icon   {
    font-size: 1rem;
    transition: 0.3s;
}
.button{
    justify-content: center;
    
    
    
}
.contact__button:hover .contact__button-icon {
    transform:translate(0.25rem) ;
}
.contact__form {
    width: 300px;
}

.contact__form-div {
    position: relative;
    margin-bottom: var(--mb-2);
    height: 4rem;
}

.contact__form-input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 1.5px solid rgba(0, 0, 0, 0.3);
    background:none;
    color: rgb(47, 44, 44);
    outline: none;
    border-radius: 0.75rem;
    padding: 1.3rem;
    z-index: 1;

}

.contact__form-tag {
    position: absolute;
    top: -1rem ;
    left: 1.25rem;
    font-size: var(--smaller-font-size);
    padding: 0.6rem;
    background-color: #fff;
    z-index: 10 ;
}

.contact__form-area {
    height: 11rem;
}

.contact__form-area textarea {
    resize: none;
}

/*=======BREAKPOINT=====*/

/*=======FOR LARGE DEVICES=====*/

@media screen and (max-width: 992px){
    .contact__container {
        column-gap: 3rem;
    }
  
  }
  
  /*=======for medium devices=====*/
  
  @media screen and (max-width: 768px){
    .contact__container{
        grid-template-columns: 1fr;
        row-gap: 3rem  ;
    }
   .contact__info {
    justify-content: center;
   }

   .contact__form {
    margin: 0 auto;
   }
  
    
  }
  
  /*=======for small devices=====*/
  @media screen and (max-width: 576px){
    .contact__info {
        grid-template-columns: 1fr;
    }

    .contact__form {
        width: 100%;
    }
  
  }
